}
body {
  background-color: var(--color-background);
  color: var(--color-foreground);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: radial-gradient(1200px 800px at 10% 0%, hsl(var(--grad-from) / 0.25), transparent 60%),
    radial-gradient(1000px 700px at 90% 10%, hsl(var(--grad-to) / 0.25), transparent 60%),
    radial-gradient(800px 500px at 50% 100%, hsl(var(--grad-alt) / 0.25), transparent 60%);
  background-attachment: fixed;
  background-repeat: no-repeat;
}
.exporting .print-hidden,
.exporting .no-print {
  display: none !important;
}
html {
  scroll-behavior: smooth;
}
section {
  scroll-margin-top: 80px;
}
.gradient-brand {
  background: linear-gradient(90deg, hsl(var(--grad-from)), hsl(var(--grad-to)));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}
.gradient-text {
  background: linear-gradient(90deg, hsl(var(--grad-from)), hsl(var(--grad-to)));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}
.hero-grid-overlay {
  pointer-events: none;
  position: absolute;
  inset: 0;
  background-image: linear-gradient(to right, rgba(17, 24, 39, 0.06) 1px, transparent 1px),
    linear-gradient(to bottom, rgba(17, 24, 39, 0.06) 1px, transparent 1px);
  background-size: 24px 24px;
  opacity: 0.25;
}
.wave-divider {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1px;
  height: 120px;
  z-index: 0;
  pointer-events: none;
}
.shimmer {
  background-image: linear-gradient(90deg, rgba(255,255,255,0) 0, rgba(255,255,255,0.18) 20%, rgba(255,255,255,0) 40%);
  background-size: 700px 100%;
}
